.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
  overflow: auto;
  background: #ffffff;
}
.textCenter {
  text-align: center;
  border: 1px solid rgb(252, 250, 250);
}
#borderBlack {
  border-bottom: 1px solid rgb(168, 162, 162);
  border-right: 1px solid rgb(168, 162, 162);
  border-left: 1px solid rgb(168, 162, 162);
}

#borderRight {
  border-right: 1px solid rgb(168, 162, 162);
  border-bottom: 1px solid rgb(168, 162, 162);
}

#grad1 {
  background-color: white; 
  background-image: linear-gradient(#fffedb, #fcf6f2, #daffd6);
}
